<template>
  <div class="invitation">
    <div class="banner" :class="$i18n.locale"></div>
    <div class="container">
      <div class="myshare">
        <div class="title">{{$t('translateLang.invitation.title1')}}</div>
        <el-row :gutter="20">
          <el-col :span="8">
            <p class="label">{{$t('translateLang.invitation.label1')}}</p>
            <table>
              <tr>
                <td>{{inviteInfo.invite_code}}</td>
                <td width="120" class="btn">
                  <el-popover
                    placement="bottom"
                    width="200"
                    popper-class="autopop"
                    trigger="click">
                    <img :src="inviteInfo.invite_url_qrcode" alt="">
                    <span slot="reference">{{$t('translateLang.invitation.btn1')}}</span>
                  </el-popover>
                  </td>
              </tr>
            </table>
          </el-col>
          <el-col :span="8">
            <p class="label">{{$t('translateLang.invitation.label2')}}</p>
            <table>
              <tr>
                <td>{{inviteInfo.invite_code}}</td>
                <td width="120" class="btn"  @click="handleCopy(inviteInfo.invite_code, $event)">
                  <span>{{$t('translateLang.invitation.btn2')}}</span>
                </td>
              </tr>
            </table>
          </el-col>
          <el-col :span="8">
            <p class="label">{{$t('translateLang.invitation.label3')}}</p>
            <table>
              <tr>
                <td>{{inviteInfo.invite_url}}</td>
                <td width="120" class="btn"  @click="handleCopy(inviteInfo.invite_url, $event)">
                  <span>{{$t('translateLang.invitation.btn3')}}</span>
                </td>
              </tr>
            </table>
          </el-col>
        </el-row>
      </div>
      <div class="statistic">
        <el-row>
          <el-col :span="8">
            <p class="title">{{$t('translateLang.invitation.title2')}}</p>
            <p>
              <span class="label">{{$t('translateLang.invitation.label4')}}</span>
              <span>{{inviteInfo.invite_num}} {{$t('translateLang.invitation.unit1')}}</span>
            </p>
          </el-col>
          <el-col :span="8">
            <p class="title">{{$t('translateLang.invitation.title3')}}</p>
            <p>
              <span class="label">USDT</span>
              <span>{{inviteInfo.reward_num}} USDT</span>
            </p>
          </el-col>
          <el-col :span="8"></el-col>
        </el-row>
      </div>
      <el-row class="nobg" :gutter="20">
        <el-col :span="12">
          <div class="invite-record">
            <div class="title">{{$t('translateLang.invitation.title4')}}</div>
            <div class="search">
              <el-select @change="getInviteRecord" size="small" v-model="inviteOptions.invite_status">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="$t('inviteCode.status'+item.value)"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>
            <el-table
              :data="inviteList"
              style="width: 100%">
              <el-table-column
                :label="$t('inviteCode.account')">
                 <template slot-scope="scope">
                  <div>
                    <span class="nickname">{{ scope.row.invite_user_nickname }}</span>
                    <span>{{scope.row.invite_user_account}}</span>
                     </div>
                </template>
              </el-table-column>
              <el-table-column
                align="center"
                :label="$t('inviteCode.status')">
                <template slot-scope="scope">
                  <div>{{ $t(`inviteCode.status${scope.row.status}`) }}</div>
                </template>
              </el-table-column>
              <el-table-column
                align="right"
                width="160"
                :label="$t('inviteCode.regtime')">
                <template slot-scope="scope">
                  <div class="time">{{ scope.row.create_time }}</div>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              background
              @size-change="(val)=>handleSizeChange(val,'inviteOptions')"
              @current-change="(val)=>handleSizeChange(val,'inviteOptions')"
              :current-page.sync="inviteOptions.page"
              :page-size="inviteOptions.per_page_num"
              layout="prev, pager, next, jumper"
              :total="totalCount1"
            ></el-pagination>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="refund-record">
            <div class="title">{{$t('translateLang.invitation.title5')}}</div>
            <div class="search">
              <el-date-picker
                :picker-options="pickerOptions"
                size="small"
                v-model="time"
                type="daterange"
                range-separator="-"
                value-format="timestamp"
                :start-placeholder="$t('queryByTime.beginPlaceorder')"
                :end-placeholder="$t('queryByTime.endPlaceorder')"
                @change="timeChange">
              </el-date-picker>
              <export-excel action="/api/User/ExportUserProfitList" :Options="exportOptions"></export-excel>
              <!-- <el-button type="primary" size="small">导出</el-button> -->
            </div>
            <el-table
              :data="refundList"
              style="width: 100%">
               <el-table-column
                :label="$t('inviteCode.account')">
                 <template slot-scope="scope">
                  <div>
                    <span class="nickname">{{ scope.row.invite_user_nickname }}</span>
                    <span>{{scope.row.invite_user_account}}</span>
                     </div>
                </template>
              </el-table-column>
              <el-table-column
                align="center"
                :label="$t('inviteCode.refund')">
                 <template slot-scope="scope">
                  <div class="time">{{ scope.row.rebate_num}} USDT</div>
                </template>
              </el-table-column>
              <el-table-column
                align="right"
                width="160"
                :label="$t('inviteCode.tradetime')">
                <template slot-scope="scope">
                  <div class="time">{{ scope.row.rebate_time | parseTime}}</div>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              background
              @size-change="(val)=>handleSizeChange(val,'refundOptions')"
              @current-change="(val)=>handleSizeChange(val,'refundOptions')"
              :current-page.sync="refundOptions.page"
              :page-size="refundOptions.per_page_num"
              layout="prev, pager, next, jumper"
              :total="totalCount2"
            ></el-pagination>
          </div>
        </el-col>
      </el-row>
      <div class="rules">
        <div class="title">{{$t('translateLang.invitation.title6')}}</div>
        <div class="content">
          <p>
          {{$t('translateLang.invitation.tips1')}}
          </p>
          <p>
          {{$t('translateLang.invitation.tips2')}}
          </p>
          <p>
          {{$t('translateLang.invitation.tips3')}}
          </p>
          <p>
          {{$t('translateLang.invitation.tips4')}}
          </p>
          <p>
          {{$t('translateLang.invitation.tips5')}}
          </p>
          <p>
          {{$t('translateLang.invitation.tips6')}}
          </p>
          <p>
          {{$t('translateLang.invitation.tips7')}}
          </p>
          <p>
          {{$t('translateLang.invitation.tips8')}}
          </p>
          <p>
          {{$t('translateLang.invitation.tips9')}}
          </p>
          <p>
          {{$t('translateLang.invitation.tips10')}}
          </p>
          <p class="tips">
            {{$t('translateLang.invitation.tips11')}}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getUserInviteInfo, getUserInviteList, getUserProfitList } from '@/api/user'
import ExportExcel from '@/components/ExportExcel'
import clipboard from '@/utils/clipboard'
import Cookies from 'js-cookie'
export default {
  name: 'invitation',
  components: { ExportExcel },
  data() {
    return {
      options: [
        { label: '全部', value: '0' },
        { label: '生效中', value: '1' },
        { label: '已失效', value: '2' },
        { label: '已无效', value: '3' }
      ],
      inviteInfo: {},
      inviteList: [],
      refundList: [],
      inviteOptions: {
        invite_status: '0',
        page: 1,
        per_page_num: 10
      },
      refundOptions: {
        page: 1,
        per_page_num: 10
      },
      exportOptions: {
        user_id: '',
        start_time: '',
        end_time: '',
        lang: Cookies.get('language') || 'zh'
      },
      time: [], // 无用
      totalCount1: 0,
      totalCount2: 0,
      pickerOptions: {
        disabledDate(time) {
          const now = Date.now()
          return time.getTime() > now
        }
      }
    }
  },
  mounted() {
    this.getInfo()
    this.getInviteRecord()
    this.getRefundRecord()
  },
  methods: {
    getInfo() {
      getUserInviteInfo().then(res => {
        if (res.status !== 1000) {
          this.$message.error(res.msg)
          return false
        }
        this.inviteInfo = res.data
        this.exportOptions.user_id = res.data.user_id
      }).catch(() => {

      })
    },
    getInviteRecord() {
      getUserInviteList(this.inviteOptions).then(res => {
        if (res.status !== 1000) {
          this.$message.error(res.msg)
          return false
        }
        this.inviteList = res.data.list
        this.totalCount1 = res.data.list_total
      }).catch(() => {

      })
    },
    getRefundRecord() {
      getUserProfitList(this.refundOptions).then(res => {
        if (res.status !== 1000) {
          this.$message.error(res.msg)
          return false
        }
        this.refundList = res.data.list
        this.totalCount2 = res.data.list_total
      }).catch(() => {

      })
    },
    showImg(pic) {
      const img = `<img
              src="${pic}"
               width="100%">`
      this.$alert(img, '', {
        dangerouslyUseHTMLString: true,
        showConfirmButton: false,
        closeOnClickModal: true,
        customClass: 'autobox'
      }).catch(() => {
        //
      })
    },
    handleCopy(text, event) {
      clipboard(text, event).then(() => {
        this.$message({
          message: this.$t('errorTips.copySuccess'),
          duration: 1000,
          type: 'success'
        })
      }).catch(() => {
        this.$message({
          message: this.$t('errorTips.copyError'),
          duration: 1000,
          type: 'warining'
        })
      })
    },
    timeChange(val) {
      console.log('val', val)
      const time = val || ['', '']
      const halfyear = (time[1] - 3600 * 1000 * 24 * 180) > time[0]
      if (halfyear) {
        this.$message.error(this.$t('translateLang.invitation.error1'))
        return
      }
      this.exportOptions.start_time = time[0] / 1000
      this.exportOptions.end_time = time[1] / 1000
    },
    handleSizeChange(val, opt) {
      this[opt].page = val
      this.getList(opt)
    },
    handleCurrentChange(val, opt) {
      this[opt].page = val
      this.getList(opt)
    },
    getList(opt) {
      switch (opt) {
        case 'inviteOptions':
          this.getInviteRecord()
          break
        case 'refundOptions':
          this.getRefundRecord()
          break
        default:
          break
      }
    }
  }
}
</script>

<style rel="stylesheet/less" lang="less" scoped>
.invitation{
  font-size: 14px;
  color: #0d202b;
  .banner{
    margin: -10px;
    margin-bottom: 0;
    height: 480px;
    background-image: url('/static/images/invitation-bg.jpg');
    background-size: cover;
    background-position: center center;
    &.en{
      background-image: url('/static/images/invitation-bg-en.jpg');
    }
    &.zh-hk{
      background-image: url('/static/images/invitation-bg-zh-hk.jpg');
    }
  }
  .container{
    margin-top: 30px;
    &>div{
      margin-top: 12px;
      background-color: #ffffff;
      border-radius: 5px;
      &>.title{
        font-size: 18px;
        padding: 35px 0 10px 25px;
        border-bottom:1px solid #f0f0f0;
      } 
    }
    .myshare{
      .el-row{
        padding: 25px 25px 36px 25px;
      }
      .label{
        font-size: 16px;
        margin-bottom: 13px;
      }
      table{
        width: 100%;
        border-collapse: collapse;
        border-radius: 3px;
        font-size: 14px;
        .btn{
          text-align: center;
          color: #1fa8f6;
          span{
            cursor: pointer;
          }
        }
        td{
          max-width: 223px;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
        }
      }
      table,th,td{
        border:1px solid#e8e6e9;
        padding: 12px;
      }
    }
    .statistic{
      padding: 30px;
      .title{
        font-size: 16px;
        margin-bottom: 20px;
      }
      .label{
        color: #8e939f;
        margin-right: 15px;
      }
    }
    .refund-record,.invite-record{
      background-color: #ffffff;
      border-radius: 5px;
      position: relative;
      .title{
        padding: 20px;
        font-size: 16px;
      }
      .search{
        position: absolute;
        right: 20px;
        top: 18px;
        .el-select{
          max-width: 104px;
        }
      }
      
      .nickname{
        display: inline-block;
        max-width: 80px;
        margin-right: 10px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
      }
      span{
        vertical-align: middle;
      }
    }
    .rules{
      margin-bottom: 40px;
      .content{
        padding: 30px;
        P{
          line-height: 30px;
        }
        .tips{
          color: #8e939f;
        }
      }
    }
  }
}

.nobg{
  background-color: transparent !important;
}

/deep/ .el-pagination.is-background{
  padding: 10px;
  text-align: center;
  .btn-next,.btn-prev,.el-pager li{
    border-radius: 13px;
    min-width: 28px;
    min-height: 28px;
    background-color: transparent;
    border: solid 1px #e8e6e9;
  }
  .el-pager li{
    min-width: 29px;
    min-height: 28px;
    &:not(.disabled).active{
    background-color: transparent;
    border: solid 1px #1fa8f6;
    color: #1fa8f6;
  }
  }
}
/deep/ .el-table{
  min-height: 538px;
    .cell{
    padding: 0 20px;
  }
}
.el-date-editor--datetimerange.el-input, .el-date-editor--datetimerange.el-input__inner{
  max-width:213px;
}

</style>
<style lang="less">
.autopop{
  padding:8px;
  text-align:center;
  width: auto !important;
  min-width: auto !important;
  img{
    display: block;
  }
}
</style>



